import "./styles.css";
import { generateData, Node, Link } from "./data-gen";
import { Graph, GraphConfigInterface } from "@cosmograph/cosmos";
import { CosmosLabels } from "./label";

const canvas = document.querySelector("canvas") as HTMLCanvasElement;
const labels = document.querySelector("#labels") as HTMLDivElement;

const cosmosLabels = new CosmosLabels<Node, Link>(labels);
let graph: Graph<Node, Link>;
const config: GraphConfigInterface<Node, Link> = {
  backgroundColor: "#151515",
  nodeSize: 4,
  nodeColor: node => node.color,
  linkColor: "#5F74C2",
  linkArrows: false,
  renderLinks: true,
  simulation: {
    linkDistance: 10,
    linkSpring: 2,
    repulsion: 1,
    repulsionTheta: 1.15,
    friction: 0.85,
    gravity: 0.25,
    onTick: () => cosmosLabels.update(graph)
  },
  events: {
    // onClick: (node, i, pos, event) => {
    //   if (node && i !== undefined) {
    //     graph.selectNodeByIndex(i);
    //     graph.zoomToNodeByIndex(i);
    //   } else {
    //     graph.unselectNodes();
    //   }
    //   console.log("Clicked node: ", node);
    // }
    onZoom: () => cosmosLabels.update(graph)
  }
};

graph = new Graph(canvas, config);

generateData()
  .then((result) => {
    const {nodes, links} = result;
    graph.setData(nodes, links);
    graph.zoom(5);
    // get max 50 modes for showing label
    const nodeIds = nodes.sort((a, b) => b.count - a.count).slice(0, 50).map(n => n.id);
    graph.trackNodePositionsByIds(nodeIds);
  });


/* ~ Demo Actions ~ */
// Start / Pause
let isPaused = false;
const pauseButton = document.getElementById("pause") as HTMLDivElement;

function pause() {
  isPaused = true;
  pauseButton.textContent = "Start";
  graph.pause();
}

function start() {
  isPaused = false;
  pauseButton.textContent = "Pause";
  graph.start();
}

function togglePause() {
  if (isPaused) start();
  else pause();
}

pauseButton.addEventListener("click", togglePause);

